}

.video-author {
  margin-bottom: 4px;
}

.video-grid {
  grid-template-columns: 1fr 1fr 1fr;
  gap: 40px 16px;
  display: grid;
}

@media (max-width: 750px) {
  .video-grid {
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: 751px) and (max-width: 999px) {
  .video-grid {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media (min-width: 1000px) {
  .video-grid {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
